<template>
  <section class="grid-card-item" @click="showDetails(stay._id)">
    <div class="grid-img">
      <span
        class="material-icons heart-icon"
        @click.stop="toggleIsLiked($event)"
        >favorite</span
      >
      <span class="demonstration"></span>
      <el-carousel
        trigger="click"
        height="262.250px"
        width="362px"
        :autoplay="false"
        :loop="false"
      >
        <el-carousel-item v-for="(item, idx) in stay.imgUrls" :key="idx">
          <img
            class="stay-grid-img-border"
            :src="
              require(`@/assets/imgs/stays/${stay.initials}/${stay.imgUrls[idx]}`)
            "
          />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="all-txt">
      <div class="flex-row">
        <div>
          <img
            class="star center-5px spacing-10px-right"
            src="../assets/imgs/icons/star.jpg"
          />
        </div>
        <div class="airbnb center gray-color rate-score-stay-line">
          <span class="rate-score-stay">{{ stay.reviews[0].avgrate }}</span> ({{
            stay.reviews[0].revnum
          }})
        </div>
      </div>
      <div class="left-details-display airbnb">
        {{ stay.loc.country }} ∙ {{ stay.typeOfPlace }}
      </div>
      <div class="left-details-display stay-name-left-details-display airbnb">
        {{ stay.name }}
      </div>
      <div class="price-details-display flex-row">
        <div class="airbnb-medium">${{ stay.price }}</div>
        <div class="airbnb">/ night</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: "stay-preview",
  props: { stay: Object },
  data() {
    return {};
  },
  methods: {
    showDetails(stayId) {
      this.$router.push({ path: `/stay/details/${stayId}` });
    },
    toggleIsLiked(ev) {
      ev.target.classList.toggle("active");
    },
  },
};
</script>
